  line-height: 16px;
  /* identical to box height */

  /* Darker Grey */
  color: #FFFFFF;
}

/* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
@media screen and (max-width:600px) {
  .column {
    width: 100%;
  }
}


